body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

@media(max-width:768px) {
  .main-container {
    flex-direction: column;
  }

  .score-container {
    flex-direction: row !important;
  }

  .instructions-container {
    padding: 20px;
  }

  .app-box-1 {
    flex: 1 1;
    padding-top: 0px !important;
  }
}

@media(max-width:1000px) {
  .score-container {
    flex-direction: column;
  }
}

.main-container, .score-container {
  display: flex;
}

.instructions-container {
  padding: 20px 30px 20px 0px;
}

.instructions-container ol > li {
  text-align: left;
  margin-bottom: 15px;
}

.app-box-1 {
  flex: 1 1;
  padding-top: 20px;
}

.app-box-2 {
  flex: 1 1;
}

.app-box-3 {
  flex: 1 1;
}

.score-box-1 {
  flex: 1 1;
  color: #0000a0;
}

.score-box-2 {
  flex: 1 1;
  color: #e80000;
}

.score-box-1 h1, .score-box-2 h1 {
  font-size: calc(50px + 2vmin);
}

.App-header {
  background-color: #282c34;
  min-height: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

table.tictactoe-tbl {
  width: 250px;
  height: 250px;
  border: #282c34 solid 5px;
  margin: auto;
}

.tictactoe-tbl tbody > tr > td {
  width: 33%;
  height: 33%;
  border: #282c34 solid 5px;
}

.main-btn button {
    background: dodgerblue;
    color: white;
    padding: 10px 30px;
    border: 0px;
    border-radius: 5px;
    font-family: calibri;
    font-size: 20px;
}

.main-btn button:hover,
.main-btn button:active {
  background: #0d60b1;
}

button {
  cursor: pointer;
}

.board-btn {
  height: 100%;
  width: 100%;
  border: 0px;
  padding: 35px;
  background: white;
}

.board-btn:hover,
.board-btn:active {
  background: lightgray;
}

.cell-mark-img {
  height: 65px;
  width: 70px;
}

.round-text {
  color: darkblue;
}

.winning_cell {
  border: #c11e04 solid 5px !important;
}

.trophy-modal {
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  border: 0px;
  background: rgb(0, 0, 0, 0.9);
  overflow: auto;
  outline: none;
  padding-top: 20px;
  text-align: center !important;
  min-height: 100%;
}

.trophy-img {
  width: 100%;
  height: 100%;
}

.trophy-img-container {
  width: calc(400px + 2vmin);
  height: calc(350px + 2vmin);
  display: inline-block;
}

.congratulations-text {
  margin-top: calc(30px + 2vmin);
  color: white;
  font-size: calc(20px + 2vmin);
  font-weight: 500;
}

.confetti-img {
  position: absolute;
  top: 10px;
  left: 50%;
  height: 80%;
  width: 100%;
}

.confetti-img img {
  position: relative;
  left: -50%;
  height: 100%;
  width: 80%;
}

.close-modal-button button {
  color: white;
  font-size: 20px;
  background: transparent;
  border: 0px;
  text-decoration: underline;
}

.close-modal-button {
  margin-top: 30px;
}
